import { Navigate, useNavigate, useParams } from "react-router-dom";

const Itm = ({ shopData, cart, setCart, sw }) => {
    const { itm } = useParams();
    //itm === shopData.id;
    const Itm = shopData.find(it => String(it.id) === itm);

    const navigate = useNavigate();

    const addCart = () => {
        // cart page  ...
        // cart    ...
        const match = cart.find(it => it.id == Itm.id)
        console.log(match)
        let option;
        if (match) {
            alert(' ...')
            option = cart.map(it => it.id === match.id ? { ...it, num: it.num + 1 } : it);
        } else {
            option = [
                ...cart,
                {
                    id: Itm.id,
                    name: Itm.name,
                    price: Itm.price,
                    desc: Itm.description,
                    img: Itm.api_featured_image,
                    num: 1
                }
            ]
        }
        setCart(option)
        navigate('/cart');
    }


    return (
        <div className="productpage">
            {
                Itm && //... ?
                <div className="product_">
                    <figure>
                        <img src={Itm.api_featured_image} alt="" />
                    </figure>
                    <div className="desc">
                        <strong>
                            {Itm.name}
                        </strong>
                        <ul className="color">
                            {
                                Itm.product_colors?.map((it, idx) => <li key={idx} style={{
                                    background: it.hex_value,
                                    display: "inline-block",
                                    width: 10,
                                    height: 10,
                                    borderRadius: '50%',
                                    margin: "0 2px"
                                }}></li>)
                            }
                        </ul>
                        <p>
                            {Itm.description}
                        </p>
                        <div className="itmPrice">
                            <span className="price">{parseInt(Itm.price * sw).toLocaleString()}</span> <span></span>
                        </div>
                        <div className="btn">
                            <button onClick={addCart}></button>
                            <button></button>
                        </div>

                    </div>
                </div>
            }
        </div>


    )
}

export default Itm;